.typed-text {
    overflow: hidden;
    width: 140px;
    border-right: .45em solid white;
    white-space: nowrap;
    letter-spacing: .15em;
    -webkit-animation: 
      typing 1.5s steps(40, end),
      blink-caret .75s step-end infinite;
            animation: 
      typing 1.5s steps(40, end),
      blink-caret .75s step-end infinite;
}

@-webkit-keyframes typing {
    from { width: 0 }
    to { width: 140px }
}

@keyframes typing {
    from { width: 0 }
    to { width: 140px }
}

@-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: white; }
}

@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: white; }
}


