.featured:before {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 41.5vw;
    z-index: -1;
    background: #1e1e1f;
    -webkit-transform: skewY(2.5deg);
    transform: skewY(2.5deg);
}

.featured-left {
    padding: 20px;
    margin-left: 5vw;
    width: 50vw;
    height: -webkit-max-content;
    height: max-content;
    position: relative;
}

.featured-right {
    margin-top: 4vw;
