.project-link-button {
    position: absolute;
    top: 50%;
    left: 43%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: white;
    width: 34%;
    height: 10%;
    border-radius: 10px;
    cursor: pointer;
    transition: opacity .4s ease-in-out;
    opacity: 0;
}

.project-link {
