    height: 50%;
    -webkit-filter: invert(100%);
            filter: invert(100%);
    padding: 8%;
    padding-top: 6%;
}

.project-link b {
    position: absolute;
    font-size: .9vw;
    width: 100%;
    color: black;
    top: 1.15vw;
    left: 5vw;
}

@-webkit-keyframes show-link {
